@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700,800,900|Open Sans:400,600,800);


.homepage {
	/* %PUBLIC_URL%/img/bg-home.jpg */
	background-image: url(/static/media/bg-home.092ced25.jpg);
	background-size: cover;
	background-position: center;
	height: 80vh;
	margin-top: 56px;
}

button {
	opacity: 0.7;
}

.highlights-ticker {
	opacity: 0.7;
}

.highlights-ticker {
	overflow: hidden;
}

.highlights-ticker-content {
    display: -webkit-flex;
    display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.highlights-ticker-content div {
    display: -webkit-flex;
    display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.highlights-ticker-content .part-1 {
    -webkit-animation: move1 10s linear infinite;
            animation: move1 10s linear infinite;
}

.highlights-ticker-content .part-2 {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
    -webkit-animation: move2 10s linear 5s infinite;
            animation: move2 10s linear 5s infinite;
}

.highlights-ticker h3 {
	margin: 10px 40px;
    white-space: nowrap;
}

@-webkit-keyframes move1 {
	0% {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
	}
	100% {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
}

@keyframes move1 {
	0% {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
	}
	100% {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
}

@-webkit-keyframes move2 {
	0% {
		-webkit-transform: translateX(-200%);
		        transform: translateX(-200%);
	}
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
}

@keyframes move2 {
	0% {
		-webkit-transform: translateX(-200%);
		        transform: translateX(-200%);
	}
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
}


@charset "utf-8";
h1,
h2,
h3,
h4,
h5,
h6,
div,
input,
p,
a {
    font-family: "Open Sans";
    margin: 0px;
}

a,
a:hover,
a:focus {
    color: inherit;
}

body {
    background-color: #F1F2F3;
}

.card-template {
    opacity: 0.5
}
.container-fluid,
.container {
    max-width: 1200px;
}



.profile-card-4 {
    max-width: 300px;
    background-color: #FFF;
    border-radius: 5px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    position: relative;
    margin: 10px auto;
    cursor: pointer;
}

.profile-card-4 img {
    transition: all 0.25s linear;
}

.profile-card-4 .profile-content {
    position: relative;
    padding: 15px;
    background-color: #FFF;
}

.profile-card-4 .profile-name {
    font-weight: bold;
    position: absolute;
    left: 0px;
    right: 0px;
    top: -70px;
    color: #FFF;
    font-size: 17px;
}

.profile-card-4 .profile-name p {
    font-weight: 600;
    font-size: 13px;
    letter-spacing: 1.5px;
}

.profile-card-4 .profile-description {
    color: #777;
    font-size: 12px;
    padding: 10px;
}

.profile-card-4 .profile-overview {
    padding: 15px 0px;
}

.profile-card-4 .profile-overview p {
    font-size: 10px;
    font-weight: 600;
    color: #777;
}

.profile-card-4 .profile-overview h4 {
    color: #273751;
    font-weight: bold;
}

.profile-card-4 .profile-content::before {
    content: "";
    position: absolute;
    height: 20px;
    top: -10px;
    left: 0px;
    right: 0px;
    background-color: #FFF;
    z-index: 0;
    -webkit-transform: skewY(3deg);
            transform: skewY(3deg);
}

.profile-card-4:hover img {
    -webkit-transform: rotate(5deg) scale(1.1, 1.1);
            transform: rotate(5deg) scale(1.1, 1.1);
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}

.card {
    width: 18rem;
    opacity: .8;
}
.quicksearch-fade-in {
    opacity: 0;
	-webkit-animation: fade-in 5s;
	        animation: fade-in 5s;
	background-color: red;
}

.quicksearch-fade-out {
    opacity: 1;
	-webkit-animation: fade-out 5s;
	        animation: fade-out 5s;
	background-color: green;
}


@-webkit-keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@-webkit-keyframes fade-out {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

@keyframes fade-out {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

